import React from 'react';
import { cssDecorator } from '@front-toolkit/utils';

import { Text } from 'core/src/typography';
import styles from './styles.scss';

const cd = cssDecorator(styles);

interface DividerProps {
  /** Название группы */
  groupName?: string;

  /** Флаг отсутсвия верхнего отступа */
  zeroTopPadding?: boolean;
}

const Divider: React.FC<DividerProps> = ({ groupName, zeroTopPadding }) => {
  if (groupName) {
    return (
      <div className={ cd('container', { zero_top_padding: zeroTopPadding }) }>
        <Text size="small" color="tertiary">{ groupName }</Text>
        <div className={ styles.wrapper }>
          <div className={ styles.divider } />
        </div>
      </div>
    );
  }

  return <div className={ styles.divider } />;
};

export default Divider;
